<template>
    <div>
        <h1 class="wrap-title mar-bottom15"></h1>
        <el-row class="mar-bottom10">
            <el-col :span="24" class="el-item pdd-10">
                <el-table :data="moduleData.list" style="width: 100%">
                    <el-table-column prop="room_id" label="#" width="70"></el-table-column>
                    <el-table-column prop="title" label="" width="150" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="location" label="" width="140"></el-table-column>
                    <el-table-column prop="seats_num" label="" width="100" ></el-table-column>
                    <el-table-column prop="manager_id" label="" width="100"></el-table-column>
                    <el-table-column prop="img_map" label="" width="130"></el-table-column>
                    <el-table-column prop="status" label=""></el-table-column>
                </el-table>
            </el-col>
        </el-row>
    </div>
</template>

<script>
//  => 
export default {
    data () {
        return {
            moduleData: {
                list: []
            }
        }
    },
    created() {
        this.getModuleData()
    },
    methods: {
        getModuleData() {
            this.$api.apiCommunication('/Rooms/getRoomsList', {}, response => {
                if (response.status === 200) {
                    this.moduleData.list = response.data.list
                } else {
                    this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                }
            })
        }
    }
}
</script>



