<template>
    <div class="home-wrap">
        <h1 class="wrap-title mar-bottom15"></h1>
        <el-tabs v-model="activeName" type="border-card">
            <el-tab-pane label="" name="info">
                <el-form ref="form" :model="form" label-width="80px" class="content-form">
                    <el-form-item label="">
                        <el-input v-model="form.name"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-select v-model="form.auth" placeholder="" filterable>
                            <el-option label="" :value="1"></el-option>
                            <el-option label="" :value="2"></el-option>
                            <el-option label="" :value="3"></el-option>
                            <el-option label="" :value="4"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.mobile"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.email"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.password"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="form.description"></el-input>
                    </el-form-item>
                    <el-form-item label=" ">
                        <el-button type="primary" size="small" @click="refresh"></el-button>
                    </el-form-item>
                </el-form>
            </el-tab-pane>
            <el-tab-pane label="" name="meeting">
                <div class=""></div>
                <el-table :data="moduleData.list" style="width: 100%">
                    <el-table-column type="index" width="50"></el-table-column>
                    <el-table-column prop="name" label="" width="250" show-overflow-tooltip></el-table-column>
                    <el-table-column prop="status" label="" width="100"></el-table-column>
                    <el-table-column label="" min-width="180">
                        <template slot-scope="scope">
                            <el-button type="primary" @click="" size="small"></el-button>
                            <el-button type="danger" @click="" size="small"></el-button>
					    </template>
                    </el-table-column>
                </el-table>
            </el-tab-pane>
        </el-tabs>
        <el-row class="mar-bottom10 mar-top10">
            <el-col :span="24" class="el-item">
                <h1 class="home-date-title"></h1>
                <datepicker :inline="true" :value="newDate" calendar-class="datepicker-cont"></datepicker>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker'
import '@/static/style/home/index.scss'
export default {
    data () {
        return {
            activeName: 'info',
            form: {
                // name: '',
                // auth: 1,
                // mobile: '',
                // email: '',
                // password: '',
                // description: ''
            },
            moduleData: {
                list: []
            },
            newDate: new Date()
        }
    },
    created() {
        this.getModuleData()
        this.getMeetingList()
    },
    methods: {
        getModuleData() {
            this.$api.apiCommunication('/Index/index', {}, response => {
                if (response.status === 200) {
                    this.form = response.data
                } else {
                    this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                }
            })
        },
        getMeetingList() {
            this.$api.apiCommunication('/Meeting/getMeetingList', {}, response => {
                if (response.status === 200) {
                    this.moduleData.list = response.data.list
                } else {
                    this.$alert(`${response.data}`, '', { confirmButtonText: '', type: 'error' })
                }
            })
        },
        refresh() {
            this.getModuleData()
        }
    },
    components: {
        Datepicker
    }
}
</script>



